.finalText{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 3rem;
}
@media (max-width: 658px) {
    .finalText{

        display: flex;
        flex-direction: column;
        padding: 0rem 1rem;
    }   
    .final1{
        width: 100%;
        display: flex;
        margin-bottom: 2rem;
    }
    .final1 img{
        height: 100%;
        width: 50%;
    }
    .textwid{
        width: 100%;
    }
}
.textwid{
    width: 30%;
}

.final1{
    display: flex;
    flex-direction: column;
    align-items: center;    
}
.final1 h1{
    letter-spacing: 2px;
    font-size: 70px;
    font-weight: lighter;
    margin: 0rem 1rem;
}
.final1 p{
    font-size: 25px;
    

}
.emptyfinal{
    width: 100%;
    height: 8px;
    background-color: brown;
    margin-top: 2rem;
}

.final1 img{
    height: 100%;
    width: 100%;
}