.runhead{
    display: flex;
    flex-direction: row;
    width: 100%;
}
.run{
    display: flex;
    width: 50%;
}
.run img{
    width: 100%;
}
.run1{
    display: flex;
    width: 50%;
    background-color: rgb(237, 42, 75);
    padding: 1rem 1rem;
}

@media (max-width: 658px){
    .runhead{
        display: flex;
        flex-direction: column;
    }
    .run{
        width: 100%
    }
    .run1{
        width: 100%;
        padding: 0;
        display: flex;
        text-align: center;
    }
    
   
}

.runText{
    display: flex;
    flex-direction: column;
    color: white;
    
}
.runText h1{
    font-size: 50px;
    font-weight: lighter;
}
.runText button{
    padding: 1rem 1rem;
    letter-spacing: 2px;
    font-weight: bold;
    margin-bottom: 1rem;
}