.totalContent{
    display: flex;
    padding: 2rem 2rem;
    justify-content: space-between;
}
@media (max-width: 658px){
    .totalContent{
        display: flex;
        padding: 2rem 2rem 2rem 0rem;
        flex-direction: column;
    }
    .imagecontent{
        display: flex;
        flex-direction: column;
        width: 100%;
    }
}
.imagecontent{
    display: flex;
    flex-direction: column;
    margin: 0.5rem 1rem;
}
.imagecontent img{
    width: 100%  
}
.imgpara{
    display: flex;
    flex-direction: column;
    height: 170px;
    background-color: white;
    padding: 1rem;
}

.imgpara p{
    margin: 0;
}