.textimg{
    display: flex;
    justify-content: space-between;
    margin: 5rem 3rem 0rem 3rem;
    padding: 3rem 0rem;
}

.textimg1{
    display: flex;
    flex-direction: column;
    text-align: center;
    margin: 0rem auto;
}
@media (max-width: 658px){
    .textimg{
        display: flex;
        flex-direction: column;
        margin: 1rem 3rem 0rem 3rem;
    }

    
}
.textimg1 img{
    width: 50px;
    height: 50px;
    margin: auto;

}
